$color-primary: #008AD8;

.course-list-table-container {
    padding: 2px;
    margin: 10px;
    margin-top: 0;
}

.course-list-table thead{
    background-color: #444444;
    color: #ffffff;
}

.course-list-table tr td {
    border: 0px;
    border-bottom: 1px solid #4444444f;
}

table.course-list-table {
    border-radius: 2px;
    border: none;
    width: 100%;
}

table.course-list-table tr {
    line-height: 30px; 
}

.course-list-table-download-options {
    flex: 1;
    text-align: right;
    margin-bottom: 22px;
}

tr.course-list-table-data-row:hover{
    cursor:pointer;
    border-radius: 2px;
    transform: scaleX(1.01);
    transition: all 0.2s ease-in-out;
    box-shadow: 0px 4px 20px 0px #00000033;
}

.edit-post-visual-editor table.course-list-table{
    border-radius: 2px;
    border: none;
    width: 100%;
}

.wisdm-ld-loading {
    width: 100%;
    min-height: 40px;
    animation: load 1.2s infinite 0s ease-in-out;
	animation-direction: alternate;
	text-shadow: 0 0 1px $color-primary;
    display:flex;
	justify-content: center;
	align-items: center;
    @keyframes load {
        0%{
            opacity: 0.08;
            filter: blur(5px);
            letter-spacing: 3px;
            }
        100%{
            
            }
    }
}

.download-csv-button {
    color:#ffffff;
}

.download-csv-button.disabled {
    cursor:not-allowed;
}

.wisdm-learndash-reports-chart-block.error {
    text-align: center;
}

.table-chart-container .table-pagination {
    text-align: center;
}

.table-pagination button {
    background-color: #ffffff;
    border: 1px solid #000000;
    color: #000000;
    border-radius: 4px;
    margin: 4px;
}

.chart-title {
    flex: 1;
    font-size: 20px;
    color: #444;
    font-weight: 700;
}

.course-table-wrap {
    max-width: 100%;
    overflow: auto;
    margin-bottom: 20px;
    padding-top: 130px;
    margin-top: -130px;
}

